export default function App() {
  const [points, setPoints] = useState(initialPoints);
  const [ignored, forceUpdate] = useReducer((x) => x + 1, 0);

  const updatePoints = (effectedTeams, factor = 1) => {
    const updated = Object.keys(effectedTeams).reduce((p, ti) => {
      p[ti] += effectedTeams[ti] * factor;
      return p;
    }, points);
    setPoints(updated);
    forceUpdate(Math.random(1));
  };

  return (
    <div className="App">
      <div className={"topSection"}>
        <LeagueTable teams={teams} points={points} />
      </div>
      <div className="third-round">
        <Fixtures teams={teams} fixtures={fixtures} setPoints={updatePoints} />
      </div>
    </div>
  );
}
