import React from "react";
import Fixture from "./Fixture";

export default function Fixtures({ fixtures, teams, setPoints }) {
  return (
    <div className={"third-round"}>
      {Object.keys(fixtures).map((round) => {
        const title = <h2>מחזור {round}</h2>;
        const fixturesElements = fixtures[round].map((f) => (
          <Fixture
            fixture={f}
            teams={teams}
            setPoints={setPoints}
            key={`${round}${f.home}`}
          />
        ));
        return (
          <div key={round}>
            {title}
            {fixturesElements}
          </div>
        );
      })}
    </div>
  );
}
