import React from "react";

export default function LeagueTable({ teams, points }) {
  return (
    <table dir="rtl" className="width-50">
      <thead>
        <th>מקום</th>
        <th>קבוצה</th>
        <th>נקודות</th>
      </thead>
      <tbody>
        {Object.keys(teams)
          .sort((a, b) => points[b] - points[a])
          .map((t, i) => (
            <tr key={t} className={`pos${i}`}>
              <td>{i + 1}</td>
              <td>{teams[t]}</td>
              <td>{Math.round(points[t], 10)}</td>
            </tr>
          ))}
      </tbody>
    </table>
  );
}
