import React, { useEffect, useState } from "react";
export default function Fixture({ fixture, teams, setPoints = () => {} }) {
  const { home, away, defaultResult } = fixture;
  const [result, setResult] = useState();

  const handleClick = (newResult) => {
    const updateResultTo = newResult === result ? null : newResult;
    setPoints(getEffected(result), -1);
    setResult(updateResultTo);
    setPoints(getEffected(updateResultTo), 1);
  };

  useEffect(() => {
    handleClick(defaultResult);
  }, [defaultResult]);

  const getEffected = function (result) {
    switch (result) {
      case "h":
        return { [home]: 3, [away]: 0 };
      case "a":
        return { [home]: 0, [away]: 3 };
      case "d":
        return { [home]: 1, [away]: 1 };
      default:
        return { [home]: 0, [away]: 0 };
    }
  };

  return (
    <div className={"single-fixture"}>
      <span
        className={`home-team ${result === "h" ? "bold" : ""} team-${home}`}
        onClick={() => handleClick("h")}
      >
        {teams[home]}
      </span>
      <span
        className={`draw ${result === "d" ? "bold" : ""}`}
        onClick={() => handleClick("d")}
      >
        נגד
      </span>
      <span
        className={`home-team  ${result === "a" ? "bold" : ""} team-${away}`}
        onClick={() => handleClick("a")}
      >
        {teams[away]}
      </span>
    </div>
  );
}
